.nav {
  display: flex;
  padding: 0 15px;

  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-start;

  background-color: #f8f8f8;
  border-bottom-color: #e7e7e7;
  border-bottom-width: 1px;
  border-bottom-style: solid;
}

@media (min-width: 768px) {
  .nav {
    flex-direction: row;
  }
}

.nav-brand {
  color: #777;
  text-decoration: none;
  font-size: 18px;
  line-height: 18px;
  padding-top: 16px;
}

@media (min-width: 768px) {
  .nav-brand {
    padding-bottom: 16px;
    padding-right: 7px;
  }
}

.nav-menu {
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 8px 0;
}

.nav-left {
}

.nav-right {
  margin-left: auto;
}

.nav-items {
}

.nav-item {
  color: #777;
  text-decoration: none;

  padding: 8px 10px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

@media (min-width: 768px) {
  .nav-item {
    padding: 17px 10px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
}

.nav-item-active {
  background-color: #e7e7e7;
}

.main {
  padding: 15px;
}
